import { Card } from "@/components/ui/card"
import { points } from "@/lib/product-list"
import Image from "next/image"
import Link from "next/link"

export default function Store() {
    return (
        <div className="container mx-auto px-4 py-8 space-y-8">
            {/* Header */}
            <div className="text-center space-y-4 opacity-0 animate-fade-in">
                <h1 className="text-4xl sm:text-5xl font-bold mb-4">
                    <span className="font-bold text-yellow-400 mb-2">
                        Toko Premium Points
                    </span>
                </h1>
                <p className="text-lg text-gray-300">
                    Beli Premium Points untuk membuka fitur eksklusif
                </p>
            </div>

            {/* Grid */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                {points.map((point) => (
                    <Link
                        key={point.id}
                        href={`/store/product/${point.id}`}
                        className="block group"
                    >
                        <Card
                            className={`
                                bg-gray-900/60 
                                border border-transparent 
                                rounded-xl 
                                overflow-hidden 
                                opacity-0 
                                animate-fade-in 
                                transition-all 
                                duration-300 
                                hover:shadow-[0_0_20px_rgba(255,215,0,0.4)] 
                                hover:border-yellow-400 
                                hover:bg-gray-900/80
                            `}
                        >
                            <div className="relative aspect-square">
                                <Image
                                    src={point.image || "/placeholder.svg"}
                                    alt={`${point.amount} Premium Points`}
                                    fill
                                    className="object-cover"
                                />
                            </div>

                            {/* Info di bawah gambar */}
                            <div className="text-center py-4 bg-gradient-to-r from-yellow-400 to-yellow-300 font-bold text-black">
                                Rp{point.amount}
                            </div>
                        </Card>
                    </Link>
                ))}
            </div>
        </div>
    )
}
